import React, { Fragment } from 'react';
import { LineChart } from '@common/charts/line-chart';
import { Trans } from '@ui/i18n/trans';
import { PolarAreaChart } from '@common/charts/polar-area-chart';
import { BarChart } from '@common/charts/bar-chart';
import { FormattedNumber } from '@ui/i18n/formatted-number';
import { GeoChart } from '@common/admin/analytics/geo-chart/geo-chart';
export function VisitorsReportCharts({
  report,
  isLoading
}) {
  const totalViews = report?.pageViews.total;
  return <Fragment>
      <LineChart colSpan="col-span-8" isLoading={isLoading} data={report?.pageViews} title={<Trans message="Pageviews" />} description={totalViews ? <Trans message=":count total views" values={{
      count: <FormattedNumber value={totalViews} />
    }} /> : null} />
      <PolarAreaChart colSpan="col-span-4" isLoading={isLoading} data={report?.devices} title={<Trans message="Top devices" />} />
      <GeoChart colSpan="col-span-7" isLoading={isLoading} data={report?.locations} title={<Trans message="Top locations" />} />
      <BarChart colSpan="col-span-5" isLoading={isLoading} data={report?.browsers} direction="horizontal" individualBarColors hideLegend title={<Trans message="Top browsers" />} />
    </Fragment>;
}